import { newE2EPage } from '@stencil/core/testing';

describe('presto-player-button', () => {
  it('renders', async () => {
    const page = await newE2EPage();
    await page.setContent('<presto-player-button></presto-player-button>');

    const element = await page.find('presto-player-button');
    expect(element).toHaveClass('hydrated');
  });
});
